import cmdCellItem from '@/components/cmd-cell-item/cmd-cell-item.vue'
import cmdAvatar from '@/components/cmd-avatar/cmd-avatar.vue'
export default{
	components:{
		cmdCellItem,
		cmdAvatar
	},
	data(){
		return{
			usermessage:{},
			isCard:true
		}
	},
	methods:{
		onLoad(options){
			if(options.type==0){
				this.isCard=true;
				this.usermessage={
					title:'银行卡提现',
					account:'工商银行'
				}
			}else{
			    this.isCard=false;
				this.usermessage={
					title:'支付宝提现',
					account:'186***7889'
				}
			}
		},
		selectCard(){
			if(this.isCard===true){
				uni.navigateTo({
			  url:'chooseBankCard',
			});
			}
		}
	}
}
